import React from "react";
import PrivateLayout from "../../../layout/Private";
import DashboardCard from "../../../containers/DashboardCard";
import { hooks } from "../../../hooks";
import { GiNewspaper } from "react-icons/gi";
import { FaUser, FaUserShield, FaBoxes } from "react-icons/fa";
const Dashboard = () => {
  const posts = hooks.usePost();
  const category = hooks.useCategory();
  return (
    <PrivateLayout name="Dashboard" pageTitle="Dashboard">
      <div className="dashboardContainer">
        <div className="cardFlex">
          <DashboardCard
            title="Posts"
            count={posts?.length}
            Icon={GiNewspaper}
            url="/"
            color="cyan"
            colorInner="lightCyan"
          />
          <DashboardCard
            Icon={FaBoxes}
            title="Category"
            count={category?.length}
            url="/"
            color="gold"
            colorInner="lightGold"
          />
          {/* <DashboardCard
            Icon={FaUser}
            title="User"
            count={category?.length}
            url="/"
            color="crimson"
            colorInner="lightCrimson"
          /> */}
          <DashboardCard
            Icon={FaUserShield}
            title="Admin"
            count={category?.length}
            url="/"
            color="purple"
            colorInner="lightPurple"
          />
        </div>
      </div>
    </PrivateLayout>
  );
};

export default Dashboard;
